<template>
<div class="my-popups">

    <div class="modal d-block">
        <div class="modal-dialog modal-dialog-centered">
            <div class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title">
                        Update Barcode:
                    </h5>
                    <button v-on:click="ClosePOP($event)" type="button" class="close" data-original-title="Close"><em class="fa fa-times"></em></button>
                </div>
                <div class="modal-body">
                    <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0">
                        <Form ref="BarcodeUpdate" v-slot="{ valid,errors }">
                            <div class="row">
                                <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left">
                                    <div class="form-group">
                                        <label>Barcode:</label><span class="text-danger">*</span>
                                        <Field :rules="{ 'required': true }" class="w-100" name="Barcode" v-slot="{ errors }">
                                            <input :class="{'form-control':true,'is-invalid':errors[0]}" type="text" placeholder="Enter Barcode" maxlength="200" name="Barcode" v-model="BarcodeArray[0].BarcodeNumber" autocomplete="off" />
                                        </Field>
                                        <span v-if="errors[0]" name="Barcode" class="invalid-feedback d-block">{{ errors[0] }}</span>
                                    </div>
                                </div>
                                <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left">
                                    <div class="form-group">
                                        <label>Serial Number:</label><span class="text-danger"></span>
                                        <Field :rules="{ 'required': false }" class="w-100" name="Serial Number" v-slot="{ errors }">
                                            <input :class="{'form-control':true,'is-invalid':errors[0]}" type="text" placeholder="Enter Serial Number" maxlength="200" name="Serial Number" v-model="BarcodeArray[0].SerialNumber" autocomplete="off" />
                                        </Field>
                                        <span v-if="errors[0]" name="Serial Number" class="invalid-feedback d-block">{{ errors[0] }}</span>
                                    </div>
                                </div>
                                <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left d-none">
                                    <div class="form-group">
                                        <label>UniqueName:</label><span class="text-danger"></span>
                                        <Field :rules="{ 'required': false }" class="w-100" name="UniqueName" v-slot="{ errors }">
                                            <input :class="{'form-control':true,'is-invalid':errors[0]}" type="text" placeholder="Enter UniqueName" maxlength="200" name="UniqueName" v-model="BarcodeArray[0].UniqueName" autocomplete="off" />
                                        </Field>
                                        <span v-if="errors[0]" name="UniqueName" class="invalid-feedback d-block">{{ errors[0] }}</span>
                                    </div>
                                </div>
                                <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left my-2">
                                    <div class="form-group">
                                        <div class="search-btm-btn">
                                            <a class="btn btn-success text-white mr-2" @click="Save()" title="Save" style="cursor: pointer;"><i class="fa fa-save pr-2"></i>Save</a>
                                            <a class="btn btn-danger text-white" @click="Cancel($event)" title="Cancel" style="cursor: pointer;"><i class="fa fa-close pr-2"></i>Cancel</a>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </Form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import DataService from '../../service/DataService'
export default {
    props: {
        BarcodeArray: {
            type: Array,
            required: true,
        }
    },
    data() {
        return {
            Remarks: '',

        }
    },
    created: function () {
        debugger

        console.log(this.BarcodeArray);
    },
    methods: {
        Save: function () {
            var vm = this;

            vm.$refs.BarcodeUpdate.validate().then(result => {
                if (result) {
                    var url = `AssetCatalogId=${vm.BarcodeArray[0].AssetCatalogueId}&Barcode=${vm.BarcodeArray[0].BarcodeNumber}&Serialnumber=${vm.BarcodeArray[0].SerialNumber}&Uniquename=${vm.BarcodeArray[0].UniqueName}`;

                    DataService.UpdateBarcode(url).then(response => {
                        if (response.data != null) {
                            if (response.data.isexist == 0) {
                                vm.ShowAlert(("Barcode is Updated Successfuly"), "success", vm.$t('Alert'));
                                vm.$parent.FetchData();
                                vm.$parent.ClosePOP();
                            } else {
                                vm.ShowAlert(vm.$t('Bar_Code_Already_Exist') +" "+ response.data.uniquename, "failure", true, vm.$t('Alert'));
                                vm.$parent.ClosePOP();
                            }
                        }
                        setTimeout(function () {
                            vm.CheckBoxBootstrap()
                        }, 100);
                    });
                }
            })
        },

        Cancel: function (item) {
            this.$emit('ClosePOP', item);
        },
        ClosePOP: function (item) {
            debugger
            this.$emit('ClosePOP', item);
        }
    }
}
</script>
