<template>
  <section>
    <loader :is-visible="isLoading"></loader>
      <div class="modal my-popups right fade show" id="manageProjectModal" role="dialog" style="display: block;">
        <div class="modal-dialog mw-1100" role="document">
          <div class="modal-content">
            <div class="modal-header mheader-user">
              <button type="button" class="close" @click="ClosePOp"><i class="fa fa-times"
                  aria-hidden="true"></i></button>
                  <h4 id="myModalLabel" class="modal-title">{{ $t("AddDeployProduct") }}</h4>
                                           <span class="user-guide">
                                           <a class="ancuserguide" data-toggle="collapse" href="#collapseReplyUG" role="button" aria-expanded="false"
                                           aria-controls="collapseExample">User Guide</a>
                                         <div id="collapseReplyUG" class="divancuserguide collapse overflow-auto">
                                           <div id="scrollbarreplyuserguideMangeView" class="custom-scrollbar-js">
                                             <div class="con" v-html="$t('UserGuideAddDeployProduct')"></div>
                                           </div>
                                         </div>
                                       </span>
            </div>

            <div class="modal-body">
              <div class="border p-3">
                <dynamic-form lang="en" ref="DeployProduct" v-if="DataLoaded" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit">
                
                  <template v-slot:tgslot-UnitPrice="{ data }">
                                       
                                       <div class="input-group">
                                        <span class="input-group-append">
                                                     <span class="input-group-text" id="spnOutOF">(USD)</span>
                                                 </span>
                                               <input type="number" min="0" oninput="validity.valid||(value='')" maxlength="10"  class="form-control" step="any"  pattern="\d+\.?\d?(?!\d)" v-on:keyup="GetAssetCost(data.fieldInfo.value,'Quantity')" v-model="data.fieldInfo.value" >
                                              
                                               </div>
                                   </template>
                                   <template v-slot:tgslot-TotalCost="{ data }">
                                       
                                       <div class="input-group">
                                        <span class="input-group-append">
                                                     <span class="input-group-text" id="spnOutOF">(USD)</span>
                                                 </span>
                                               <input type="number"  oninput="validity.valid||(value='')" class="form-control" step="any"  pattern="\d+\.?\d?(?!\d)" v-on:keyup="GetUnitPrice(data.fieldInfo.value)" v-model="data.fieldInfo.value" >
                                              
                                               </div>
                                   </template></dynamic-form>

              </div>

              <div class="modal-footer">
                <div class="col-lg-12">
                  <small class="text-danger">Fields marked with an asterisk (*) are mandatory.</small>
                </div>


              </div>

            </div>


          </div>

        </div>
        <AddproducLocation v-if="Addlocation"  @closepopup="closepopup"></AddproducLocation>
      </div>
      

    </section>

</template>
<script>
import DataService from '../../service/DataService';
import AddproducLocation from '../../components/Modal/AddproducLocation.vue';
import { TreeSelectField } from 'tg-controls_cli/dist/myLib.common'


export default {

  components: {
    AddproducLocation,
    'tree-view':TreeSelectField
  },

  data() {
    return {
      FormSchema: [
        {
          layoutType: "double",
          Data: [
            {
              astype: "RadioField",
              label: this.$t('DeploymentType'),
              name: "DeployMentType",
              value: "1",
              placeholder: "",
              config: {
                        options: [{
                            value: "1",
                            name: "Product"
                        }, {
                            value: "2",
                            name: "Inventory"
                        }],
                        onChange: this.OnDeplymentType
                    },

              validationRules: { "required": false },

            },

          ]
        },

        {
          layoutType: "double",
          Data: [
            {
              astype: "SelectField",
              label: this.$t('ProductType'),
              name: "ProductType",
              value: "",
              placeholder: "Select",
              config: {
                options: [],
                //  option_request_url:process.env.VUE_APP_API_URL+'/AssetAssignment/GetAssetType?type=&issueTo=&locationId=&requestType=ASSIGN&requestFrom=',
                //  token:this.GetUserInfo.Token,
                showAddIcon: false,
                onChange: this.onChangeDropdown,
              },
              validationRules: "required",
              dependent_show_type: 0
            },

            {
              astype: "SelectField",
              label: this.$t('ProductName'),
              name: "ProductName",
              value: "",
              placeholder: "",
              config: {
                options: [],
                showAddIcon: false,
                onAddButtonClick: function (e, field) {
                }                
              },
              validationRules: {
                "required": true
              },
            },

            {
              astype: "TreeSelectField",
              label: this.$t('Location'),
              name: "Location",
              value: null,
              placeholder: "Select Location",
              config: {
                options: [],
                //showAddIcon: true
              },
              onAddButtonClick: this.OpenPopup,
              validationRules: { "required": true },
            },

            {
              astype: "NumericField",
              label: this.$t('Quantity'),
              name: "Quantity",
              value: "0",
              placeholder: "",
              visibility: true,
              config: {
                onKeyUp: this.GetAssetCost
              },
              validationRules: { "required": true, max: 7, "min_value": 1 },
            },
            {
              astype: "SlotField",
              label: this.$t('UnitPrice'),
              name: "UnitPrice",
              value: "0",
              placeholder: "",
              validationRules: { max: 10 },
              visibility: true,
              config: {
                onKeyUp: this.GetAssetCost

              },

            },
            {
              astype: "SlotField",
              label: this.$t('TotalCost'),
              name: "TotalCost",
              value: "0",
              placeholder: "",
              visibility: true,
              config: {
                onKeyUp: this.GetUnitPrice

              },
            },
          ]
        }
      ],

      buttons: [
        {
          type: "submit",
          class: "btn btn-success",
          text: "<i class='fa fa-save pr-2'></i>Save and Close",
        },

        {
          type: "button",
          class: "btn btn-danger",
          text: "<i class='fa fa-close pr-2'></i>Cancel",
          onClick: this.ClosePOp
        }
      ],
      LocationData: [],
      myObj: [],
      ProductypeID: '',
      UserId: null,
      Addlocation: false,
      DataLoaded:0,
      isLoading:false,
    }
    
  },
  async created() {
    var vm = this;
    await vm.GetLocation();
    (vm.FormSchema)[0].Data[0].value = 1
    
     await vm.BindItemTypeByUserId();
    // vm.GetCompanyAssestCatalogs();
    // vm.onChangeDropdown();
    // vm.submit();
    this.UserId = this.GetUserInfo.ID;
   
    vm.DataLoaded=1;
  },
  methods:
  {
    OnDeplymentType(event, field) {
            field.value = event.target.getAttribute('data-value');
        },
    GetAssetCost(evt, val) {
            var vm=this;
          
            if (val.label == 'Quantity' || val == 'Quantity') {
                (vm.FormSchema)[1].Data[3].value = (vm.FormSchema)[1].Data[3].value.replace(/[^0-9]/g, "");
            }
            var quantity = (vm.FormSchema)[1].Data[3].value;
            var quantityField= (vm.FormSchema)[1].Data[3];
            if (quantity.startsWith('0') && quantity.length > 1) {
              quantity = quantity.slice(1);
            }
            if (quantity.length > 7) {
              quantity = quantity.slice(0, 7);
            }
            quantityField.value = quantity;
            var unitPrice = (vm.FormSchema)[1].Data[4].value;
            var unitPriceField = (vm.FormSchema)[1].Data[4];
            if (unitPrice.startsWith('0')&& unitPrice.length > 1) {
                unitPrice = unitPrice.slice(1);
            }
            if (unitPrice.length > 5) {
                unitPrice = unitPrice.slice(0, 5);
                vm.ShowAlert(vm.$t("The Unit Price field may not be greater than 5 characters"), "failure", vm.$t('Alert'));
            }
            unitPriceField.value = unitPrice;
            if (unitPrice == "") { unitPrice = 0; $('#UnitPrice').val(unitPrice); }
            var assetCost = parseFloat(unitPrice) * parseFloat(quantity);
            if (quantity == 0) assetCost = 0;
            (vm.FormSchema)[1].Data[5].value=assetCost.toFixed(2);
        },
        GetUnitPrice(evt, val) {
            var vm=this;
            var quantity = (vm.FormSchema)[1].Data[3].value;
            var totalCost = (vm.FormSchema)[1].Data[4].value;
            var unitPrice = 0;
            if (totalCost == "") { 
                totalCost = 0; 
                (vm.FormSchema)[1].Data[4].value=(unitPrice);
             }
            unitPrice = parseFloat(totalCost) / parseFloat(quantity);
            if (quantity == 0) unitPrice = 0;
            (vm.FormSchema)[1].Data[4].value=unitPrice.toFixed(2);
        },
    async  GetLocation() {
            debugger;
            var vm = this;
            vm.isLoading = true;
            var params = `reqForm=`
            await DataService.GetLocationByuserId(params).then(response => {
                vm.LocationData = response.data.data;             
               
                vm.FindalLocationArray = [];
                vm.LocationData.forEach((value) => {
                    var CheckIfExist = vm.FindalLocationArray.filter(function (el) {
                        return el.COMPANY_LOCATION_ID == value.COMPANY_LOCATION_ID;
                    });
                    if (CheckIfExist.length == 0) {
                        if (value.PARENT_LOCATION_ID == null) {
                            let locationId = value.COMPANY_LOCATION_ID
                            let rslt = vm.getChildLocation(locationId, value, response.data.data)
                            vm.FindalLocationArray.push(rslt)
                        }
                    }
                });
                (vm.FormSchema)[1].Data[2].config.options = vm.FindalLocationArray;
                vm.isLoading = false;
            })
        },
        getChildLocation: function (locationId, currentItem, data) {
            let ths = this;
            let treeArray = {};
            treeArray = {
                id: currentItem.COMPANY_LOCATION_ID,
                label: currentItem.LOCATION_NAME
            }
            let retrunArray = treeArray;
            var childArray = data.filter(function (el) {
                return el.PARENT_LOCATION_ID == locationId;
            });
            let finalChildArray = [];
            childArray.forEach((value) => {
                let ChildfolderId = value.COMPANY_LOCATION_ID
                var CheckIfExist1 = finalChildArray.filter(function (el) {
                    return el.COMPANY_LOCATION_ID == value.COMPANY_LOCATION_ID;
                });
                if (CheckIfExist1.length == 0) {
                    var CheckIfExist = data.filter(function (el) {
                        return el.PARENT_LOCATION_ID == ChildfolderId;
                    });
                    if (CheckIfExist != null && CheckIfExist.length > 0) {
                        let rslt = ths.getChildLocation(ChildfolderId, value, data);
                        finalChildArray.push(rslt);
                    } else {
                        treeArray = {
                            id: `${value.COMPANY_LOCATION_ID}`,
                            label: `${value.LOCATION_NAME}`
                        }
                        finalChildArray.push(treeArray);
                    }
                }
            });
            retrunArray.children = finalChildArray;
            return retrunArray;
        },
    
    // GetCompanyAssestCatalogs: function (ths) {
    //   let url = `id=${ths}&issueTo=${this.UserId}&locationId=${this.txtLocation}&requestFrom=assetassignment`;
    //   DataService.GetCompanyAssestCatalogs(url).then(response => {
    //     this.ProducttData = response.data;
    //   });
    // },
    calcSum(val) {
      var vm = this
      let totalcost = 0;
      totalcost = (vm.FormSchema)[1].Data[3].value * (vm.FormSchema)[1].Data[4].value;
      (vm.FormSchema)[1].Data[5].value = totalcost;

    },
    async BindItemTypeByUserId () {
      
      let url = `type=&issueTo=&locationId=&requestType=ASSIGN&requestFrom=`;
      var vm = this;
      await DataService.AssignGetAssetType(url).then(response => {
        response.data.forEach((itm, index) => {
          (vm.FormSchema)[1].Data[0].config.options.push({
            name: itm.ASSETTYPE,
            value: itm.ASSETID
          })
          
        })
      });
    },
    async onChangeDropdown(evt) {      
      var vm = this;
      var params = `assetTypeid=${evt.target.value}`
     await DataService.GetAssetCatalogueType(params)
        .then(function (response) {
          response.data.forEach((itm, index) => {
            (vm.FormSchema)[1].Data[1].config.options.push({
              name: itm.NAME,
              value: itm.ASSETID
            })
            vm.$refs.DeployProduct.UpdateKeyValue();
          })
        });
    },
    OpenPopup: function () {
      var vm = this
      vm.Addlocation = true
    },
    ClosePOp() {
      var vm = this;
      vm.$parent.ClosePOp()
    },
    closepopup() {
      var vm = this
      vm.Addlocation = false;
    },
    onSubmit(formfields, e) {
     var vm=this;
     vm.isLoading = true;
      var ArrayData = [];
      let myObj = {
        DeployId: 0,
        AssetCatalogueId: formfields.ProductName,
        AssetTypeId: formfields.ProductType,
        LocationId: formfields.Location,
        LocationName: null,
        Quantity: formfields.Quantity,
        UnitPrice: formfields.UnitPrice,
        Model: null,
        Manufacturer: null,
        Vendor: null,
        PurchaseOrder: null,
        WarrantyDuration: null,
        DepreciationRuleId: 0,
        OrderDate: null,
        CalibrationFrequency: null,
        AssetCost: formfields.TotalCost,
        SalvageCost: null,
        ItemLife: null,
        InvoiceNumber: null,
        InvoiceDate: null,
        InsurerName: null,
        InsuranceRefNumber: null,
        InsuranceTo: null,
        DeployedBy: null,
        DeployedAt: "0001-01-01T00:00:00",
        EnableWarranty: false,
        EnableInsurance: false,
        EnableItemCost: false,
        EnableCalibration: false,
        EnableDepreciation: false,
        DeploymentType: formfields.DeployMentType,
      }
      ArrayData.push(myObj);
      DataService.SaveDeploy(ArrayData).then(function (response) {
        if (response.data.result == "success") {
          vm.ShowAlert(vm.$t("AddProductDeploy"), "success", vm.$t('Alert'));
          vm.ClosePOp();
          vm.isLoading = false;
        }
        else {
                vm.isLoading = false;
                vm.ShowAlert(vm.$t("Unauthorized Access"), "warning", vm.$t('Alert'));
            }
      })

    }
  }
}
</script>


