                            column.VISIBLE = true;
                            column.DATA_TYPE = "text";
                            column.settings = {
                                isInSlot: true
                            }
                        }
                        if (column.COLUMN_NAME == 'Calendar') {
                            column.settings = {
                                isInSlot: true
                            }

                        }
                        if (column.COLUMN_NAME == 'HISTORY') {
                            column.settings = {
                                isInSlot: true
                            }

                        }
                        if (column.COLUMN_NAME == 'ACTION') {
                            column.settings = {
                                isInSlot: true
                            }

                        }
                        if (column.COLUMN_NAME == 'status_id') {
                            column.DISPLAY_NAME = 'Status'
                            column.settings = {
                                isInSlot: true,
                                isForTag: true
                            }
                        }
                        // if (column.COLUMN_NAME == 'client_first_name') {
                        //     column.settings = {
                        //         clickEvent: vueObj.clickEventOfColumn,
                        //         formatter: vueObj.columnDataFormatter
                        //     }
                        // }
                        // if (column.COLUMN_NAME == 'status_id') {
                        //     column.settings = {
                        //         isInSlot: true
                        //     }
                        // }
                    });
                }
                if (response.data.data.length > 0) {
                    vm.RelatedInformationData = response.data.data;
                    vm.RelatedInformationData.forEach((row) => {
                        row.isCheckBoxDisabled = false;
                        row.additionalClass = '';
                        // if (row.status_id == 'Closed') {
                        //     row.additionalClass = 'warning-bar';
