                        // row.rowDetailsUrl='';
                        // if(row.rowDetailsUrl=='')
                        // {
                        //     row.rowDetailsUrl='/CRM/ViewLeadDetails/' + row.lead_id + '/CRM/CRM_LEADS';
                        // }
                    });
                    vm.TotalRecords = response.data.data[0].total_records;
                    vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                    vm.CurrentPage = vm.PageNumber;
                    vm.noRecord = false;
                } else {
                    vm.noRecord = true;
                    vm.RelatedInformationData = [];
                    vm.TotalRecords = 0
                    vm.isLoadings = false;
                }
                // if (response.data != null) {
